body {
    margin: 0;
    padding: 0;
    background-color: #4B92DB;
}

.map-container {
    width: 100%;
    height: 100%;
    position: relative;
}

.wind-arrow {
    position: absolute;
    border: solid;
    opacity: 0;
    border-width: 0 3px 3px 0;
    padding: 3px;
}

#world-map {
    position: relative;
    width: 100%;
    height: 100%;
}

.scale-container {
    position: absolute;
    bottom: 20px;
    left: 20px;
    padding: 5px 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: system-ui;
}

.scale-bar {
    width: 64px;
    height: 8px;
    border-radius: 4px;
    background-color: #000;
    margin-bottom: 4px;
}

.scale-text {
    font-size: 12px;
    color: #000;
}