body {
  background-color: #4b92db;
  margin: 0;
  padding: 0;
}

.map-container {
  width: 100%;
  height: 100%;
  position: relative;
}

.wind-arrow {
  opacity: 0;
  border: 3px solid;
  border-width: 0 3px 3px 0;
  padding: 3px;
  position: absolute;
}

#world-map {
  width: 100%;
  height: 100%;
  position: relative;
}

.scale-container {
  flex-direction: column;
  align-items: center;
  padding: 5px 10px;
  font-family: system-ui;
  display: flex;
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.scale-bar {
  background-color: #000;
  border-radius: 4px;
  width: 64px;
  height: 8px;
  margin-bottom: 4px;
}

.scale-text {
  color: #000;
  font-size: 12px;
}
